import { Injectable, Injector } from '@angular/core'
import { HttpEvent, HttpHandler, HttpInterceptor, HttpResponse } from "@angular/common/http";
import { HttpRequest } from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import { Router } from "@angular/router";
import { JWtAuthService } from './auth.service';


@Injectable()
export class AuthInterceptorService implements HttpInterceptor {

    constructor(/*private auth: AuthService,*/ private router: Router, private injector: Injector) { }
    intercept(req, next) {
        const auth = this.injector.get(JWtAuthService);
        //const authToken = auth.getAuthorizationToken();

        var authRequest = req.clone({
            headers: req.headers.set('Authorization', 'token ' + auth.token)
        })
        return next.handle(authRequest);
    }
}


