import { Component, OnInit } from '@angular/core';
import { DataSource } from '@angular/cdk/collections';
import { ApiService } from '../api.service';
import { MatTableModule } from '@angular/material/table';
import { Sort } from '@angular/material';

@Component({
  selector: 'app-viewleave',
  templateUrl: './viewleave.component.html',
  styleUrls: ['./viewleave.component.css']
})
export class ViewleaveComponent implements OnInit {

  constructor(private apiService: ApiService) {
  }

  private Leaves;

  getLeaves() {
    console.log("client side leaves:" + this.apiService.leaves)
  }

  getLeaveStatus(leaveStat) {
    let lsFlag=false
    for (let i = 0; i < leaveStat.length; i++) {
      if (leaveStat[i].leaveStatus == "approved") {
        lsFlag=true
        break
      }
    }
    if(lsFlag==true)
    {
      return "approved"
    }
    else
    {
      return "pending"
    }
  }

  ngOnInit() {
    this.apiService.getUsersLeaves({"email":localStorage.getItem("laUser").toString()});
  }



}




