import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core'
import { environment } from '../environments/environment'
import { Router } from '@angular/router';

@Injectable()
export class JWtAuthService {

    messages = [];

    path = environment.path + '/auth';//"http://localhost:3000";//'http://localhost:3000/auth';

    TOKEN_KEY = 'token';

    constructor(private http: HttpClient, private router: Router) { }

    get token() {
        return localStorage.getItem(this.TOKEN_KEY);
    }

    get isAuthenticated() {
        return !!localStorage.getItem(this.TOKEN_KEY);
    }

    logout() {
        
        localStorage.removeItem(this.TOKEN_KEY);
        this.redirectToHome();
        
    }

    redirectToHome() {
        this.router.navigate(['/']);
    }

    registerUser(registerData, isRegistered) {
        this.http.post<any>(this.path + '/register', registerData).subscribe(res => {
            //console.log(res);
            this.saveToken(res.token);
            isRegistered(true);

        }, err => {
            isRegistered(false);
        });
    };

    logInUser(loginData, isLoginedIn) {
        this.http.post<any>(this.path + '/login', loginData).subscribe(res => {
            this.saveToken(res.token);
            isLoginedIn(true);
        }, err => {
            isLoginedIn(false);
        });
    }

    saveToken(token) {
        localStorage.setItem(this.TOKEN_KEY, token);
    }
}


