import { Component, OnInit } from '@angular/core';
import { DataSource } from '@angular/cdk/collections';
import { ApiService } from '../api.service';
import { MatTableModule } from '@angular/material/table';
import { Sort } from '@angular/material';
@Component({
  selector: 'app-approve-leave',
  templateUrl: './approve-leave.component.html',
  styleUrls: ['./approve-leave.component.css']
})
export class ApproveLeaveComponent implements OnInit {

  constructor(private apiService: ApiService) {
  }

  private Leaves;
  
    getLeaves() {
      console.log("client side leaves:" + this.apiService.leaves)
    }
  
    getLeaveStatus(leaveStat) {
      let lsFlag=false
      for (let i = 0; i < leaveStat.length; i++) {
        if (leaveStat[i].leaveStatus == "approved") {
          lsFlag=true
          break
        }
      }
      if(lsFlag==true)
      {
        return "approved"
      }
      else
      {
        return "pending"
      }
    }
    ngOnInit() {
      debugger;
      this.apiService.getUsersLeavesAssignedToManager({"email":localStorage.getItem("laUser").toString()});
    }
    approveLeave(leaveId, leaveType, days, userEmail){
      debugger
      let leaveDetails =
      {
        "managerEmail":localStorage.laUser,
        "leaveId": leaveId,
        "userEmail": userEmail,
        "leaveType": leaveType,
        "days":days
      } 
      console.log("manager Email: "+localStorage.laUser)
      this.apiService.approveLeave(leaveDetails)
    }
}



