import { Component } from '@angular/core';
//import { JWtAuthService } from './auth.service';
import { AuthService } from 'angular4-social-login';
import { SocialUser } from 'angular4-social-login';
import { GoogleLoginProvider/*, FacebookLoginProvider*/ } from 'angular4-social-login';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']

})

export class AppComponent {
  title = `my app`;

  // constructor(/*private authService: JWtAuthService*/) { }

  user: SocialUser;
  private allowedEmailDomain = "cctech.co.in";

  constructor(private authService: AuthService, private router: Router) { }

  ngOnInit() {
    this.authService.authState.subscribe((user) => {
      debugger;
      this.user = user;
      if (this.user) {
        if (this.user.email.split('@')[1] != this.allowedEmailDomain)
          this.authService.signOut();
        else
          this.router.navigate(['/leaves']);
      }
      else {
        this.router.navigate(['/']);
      }
    });
  }

  // signInWithGoogle(): void {
  //   this.authService.signIn(GoogleLoginProvider.PROVIDER_ID);
  // }

  // signInWithFB(): void {
  //   this.authService.signIn(FacebookLoginProvider.PROVIDER_ID);
  // }

  signOut(): void {
    this.authService.signOut();
    localStorage.removeItem("laUser");
  }
}



