import { Component, OnInit } from '@angular/core';
import { JWtAuthService } from '../auth.service';
import { Router } from '@angular/router';
import { debug } from 'util';
import { DomSanitizer } from '@angular/platform-browser';
import { MatIconRegistry } from '@angular/material';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  hide = true;
  loginData = {};
  private logInCB;

  constructor(private authService: JWtAuthService, private router: Router) { }

  post() {
    this.authService.logInUser(this.loginData, (success) => {
      if (success)
        this.router.navigate(['/leaves']);//, { queryParams: { id: '123' }});
    })
  }


  ngOnInit() {
    if (this.authService.isAuthenticated)
      this.router.navigate(['/leaves']);//, { queryParams: { id: '123' }});
    else
      this.router.navigate(['/']);
  }

}



