import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core'
import { environment } from '../environments/environment'


@Injectable()
export class ApiService {

    messages = [];
    users = [];
    leaves = [];
    leavesMgr=[];
    

    //path = environment.path;//"http://localhost:3000";
    //path = "http://localhost:3000";
    path = "https://leave-application-server.herokuapp.com";

    constructor(private http: HttpClient) { }

    getMessages(userId) {
        this.http.get<any>(this.path + '/posts/' + userId).subscribe(res => {
            console.log(res);
            this.messages = res;
        });
    }

    getLeaves() {
        this.http.get(this.path + '/getleaves').subscribe(res => {
            console.log(res);
        });
        // this.http.post<any>(this.path + '/getleaves',{}).subscribe(res => {
        //     console.log(res);
        // });
    }

    getUsers() {
        this.http.get<any>(this.path + '/users').subscribe(res => {
            console.log(res);
            this.users = res;
        });
    }

    getProfile(id) {
        return this.http.get<any>(this.path + '/profile/' + id);
    }

    postMessage(postMsg) {
        this.http.post(this.path + '/post', postMsg).subscribe(res => {
        });
    }

    applyForLeave(postMsg) {
        
        //this.http.post(this.path + '/leave/applyforleave', postMsg).subscribe(res => {
            this.http.post(this.path + '/leave/apply', postMsg).subscribe(
                res => {
                console.log("leave applied ....."+res)},
                err => {
                    console.log("Error occured "+err);
                    //callback(err, "err");
                  }
            );
    }

    getUsersLeaves(userId) {
        debugger;
        //this.http.get<any>(this.path + '/leave/getLeaves/' + userId).subscribe(res => {
        //this.http.post<any>(this.path + '/leave/getLeaves',{}).subscribe(res => {
        this.http.post<any>(this.path + '/leave/leavehistory', userId).subscribe(
            res => {
                console.log("view leave  ....." + res);
                this.leaves = res;
            },
            err => {
                console.log("Error occured " + err);
                //callback(err, "err");
            }
        );
    }

    getUsersLeavesAssignedToManager(userId) {
        debugger;
        //this.http.get<any>(this.path + '/leave/getLeaves/' + userId).subscribe(res => {
        //this.http.post<any>(this.path + '/leave/getLeaves',{}).subscribe(res => {
        this.http.post<any>(this.path + '/leave/getappliedleavesmgr', userId).subscribe(
            res => {
                console.log("view leave  ....." + res);
                this.leavesMgr = res;
            },
            err => {
                console.log("Error occured " + err);
                //callback(err, "err");
            }
        );
    }

    approveLeave(leaveDetails)
    {
        this.http.post<any>(this.path + '/leave/approve', leaveDetails).subscribe(
            res => {
                console.log("view leave  ....." + res);
                this.leavesMgr = res;
            },
            err => {
                console.log("Error occured " + err);
                //callback(err, "err");
            }
        );
    }
}


