import { Component, OnInit } from '@angular/core';
import { JWtAuthService } from '../auth.service';
import { Router } from '@angular/router';
import {MatRadioModule} from '@angular/material/radio';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  
  private registerData = {
    gender:''
  };

  constructor(private authService: JWtAuthService, private router: Router) { }

  getval(car){
    this.registerData.gender =car;
  }

  post() {
    console.log(this.registerData);
    debugger;
    this.authService.registerUser(this.registerData,(success) => {
      if(success)
        this.router.navigate(['/leaves']);//, { queryParams: { id: '123' }});
    });
  }

  ngOnInit() {
  }

}



