import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  constructor(private apiService: ApiService, private route: ActivatedRoute) { };
  
      profile;
  
      ngOnInit() {//gets called once component is intialized if there is any delay there won't be any issues caused
  
          let _id = this.route.snapshot.params.id;
          this.apiService.getProfile(_id).subscribe(data => this.profile = data);
      }

}



